}

.typejs {
  color: #00C86F;
}

span {
  color: #00C86F;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3,
h4 {
  font-family: "Roboto", sans-serif;
  margin: calc(separacion / 2) 0;
  font-weight: 900;
  color: #FFFFFF;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

span {
  color: white;
}

.main {
  text-align: left;
  padding: 4rem 0 0 30rem;
  background: linear-gradient(45deg, #051933, #164074);
  background-size: 200% 200%;
  animation: gradientAnimate 4s ease infinite;
  min-height: 100vh;
}
@media (max-width: 700px) {
  .main {
    width: 100vw;
    text-align: left;
    padding: 2rem;
    background: linear-gradient(45deg, #051933, #164074);
    background-size: 200% 200%;
    animation: gradientAnimate 4s ease infinite;
    min-height: 100vh;
  }
}
@keyframes gradientAnimate {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

section {
  min-height: 100vh;
  max-width: 85%;
}
@media (max-width: 700px) {
  section {
    max-width: 100%;
    min-height: 70vh;
  }
}

.boton {
  border: none;
  padding: 1rem;
  border-radius: 0.4rem;
  text-transform: uppercase;
  background-color: #A3FFBF;
  color: white;
  box-shadow: 0px 0px 20px rgba(0, 200, 111, 0.5);
}
@media (max-width: 700px) {
  .boton {
    padding: 2rem;
    font-size: 1.5rem;
  }
}

.contenido-centrado {
  max-width: 800px;
}

.header {
