  display: block;
  padding: 4rem;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}
@media (max-width: 700px) {
  .header .header__contenedor {
    display: flex;
    padding: 1rem;
    align-items: center;
    gap: 1rem;
  }
}
.header .header__contenedor .header__titulo {
  color: #0B3E7D;
  padding-bottom: 1rem;
  border-bottom: 2px solid white;
}
@media (max-width: 700px) {
  .header .header__contenedor .header__titulo {
    color: white;
    border-bottom: 2px solid white;
    font-size: 1.3rem;
  }
}
.header .header__contenedor .header__imagen {
  background: url(/build/img/bmo.webp) center center/cover no-repeat;
  height: 15rem;
  width: 15rem;
  border-radius: 50%;
  border: 1px solid #00C86F;
  box-shadow: 0px 0px 20px rgba(0, 200, 111, 0.5);
}
@media (max-width: 700px) {
  .header .header__contenedor .header__imagen {
    display: none;
  }
}
.header .header__contenedor .header__navegacion {
  display: flex;
  flex-direction: column;
}
@media (max-width: 700px) {
  .header .header__contenedor .header__navegacion {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
  }
}
.header .header__contenedor .header__navegacion a {
  color: white;
  font-size: 2rem;
}
@media (max-width: 700px) {
  .header .header__contenedor .header__navegacion a {
    color: white;
    font-size: 1.2rem;
  }
}
.header .header__contenedor .header__sociales {
  display: flex;
